@import "../extends.scss";
.game {
  display: flex;
  flex-direction: column;
  width: 500px;
  margin: 0 auto;
  align-items: center;
  background-color: white;
  height: calc(100vh - 50px);
  min-height: 560px;
}

.game_board {
  margin: 0 auto;
  background-color: white;
}

.game_title {
  height: 70px;
  display: flex;
  align-items: center;
}

%button {
  color: white;
  font-size: large;
  padding: 10px;
  cursor: pointer;
  margin-bottom: 5px;
  width: 300px;
}

.button__prime:active {
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
}

.button__secondary {
  background-color: $colorGray__dark;
  @extend %button;
}
.button__secondary:hover,
.button__secondary:active {
  background-color: $colorGray__darker;
}

.game__green {
  background-color: white;
  .button__prime {
    background-color: $colorGreen__prime;
    @extend %button;
  }
  .button__prime:hover,
  .button__prime:active {
    background-color: $colorGreen__dark;
  }
}
.game__orange {
  background-color: white;
  .button__prime {
    background-color: $colorOrange__prime;
    @extend %button;
  }
  .button__prime:hover,
  .button__prime:active {
    background-color: $colorOrange__dark;
  }
}